    height: 1.5rem;
    padding: 0.7rem 3rem;
    border-radius: 0.8rem;
    background-color: var(--primary-color);
    color:var(--text-color);
    margin-top:1.5rem;
    max-width: 60%;
}
.GameButtons button:hover{
    transition:200ms;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: var( --primary-color-dark) ;
}

.GameTimer{
    background: var(--section-bg-color);
    padding: 0.5rem 2rem;
    width: 10rem;
    text-align: center;
    margin-top: 1rem;
}
.GameTimer > div{
    color: var(--text-color);
    font-size: 2.5rem;
    opacity: 0.6;
}

.GameTimer .active{
    opacity: 1;
    -webkit-animation: pulsate 3s ease-out;
    -webkit-animation-iteration-count: infinite;
}

.GameTimer .low-time{
    color: var(--fail-color);
}


@-webkit-keyframes pulsate {
    0% {
        opacity: 0.5;
    }
    50% {
        opacity: 1.0;
    }
    100% {
        opacity: 0.5;
    }
}

.GameTimersWidget{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 2rem;
}
.TurnIndicator {
    -webkit-order:1;
            order:1;
    background-color: var(--chessboard--white);
    color: black;
    font-size: 1.5rem;
    margin-top: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 70%;
    padding: 0.7rem 1.2rem;
}



.TurnIndicator h1{
    all: unset;
    font-size: 1.7rem;
}
